body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.pageHeader{
  background-image: url('https://st.depositphotos.com/2121483/3263/v/450/depositphotos_32630089-stock-illustration-modern-minimalistic-furniture-interior-design.jpg');
  height: 170px;
  margin-bottom: 2%;
}
.bookListHeader{
  /* display: inline; */
  font-size: 50px;
  text-align: center;
  color:black;
  /* width: 300px; */
}

#Books {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse:collapse;
  width: 90%;
  margin:0 auto;

}

#Books td, #Books th {
  border: 1px solid #ddd;
  padding: 8px;
  width: 30%;
  vertical-align: top;
}

#Books tr:hover {background-color: #ddd;}

#Books th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: rgb(234, 247, 242);
  color:black;
  font-size: 20px;
}
/*   background-color: rgb(225, 255, 245); */
.header{
  font-size: 20px;
}
.imageBook{
  height:auto;
  width: 200px;
}

.checked {
  text-decoration: line-through;
  color: grey;
}

.NoBook{
  text-align: center;
  margin-top: 100px;
  font-size: 40px;
  color:  rgb(1, 6, 10);
}
.About{
  font-size: 25px;
  color: rgb(115, 160, 228);
  margin-left: 20px;
  text-align: center;
}
.form{
   width: 20% !important;
}
.btn{
  background-color: rgb(234, 247, 242) !important ;
  border:1px solid rgb(129, 126, 126) !important ;
  width: 130px;
}
.btn1{
  margin-bottom: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-left: 30px;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.btnCheckedClear{
  border:1px solid rgb(129, 126, 126) !important ;
  border-radius: 0.25rem;
  width: 180px;
  height: 30px;
}
.count{
  border-bottom:1px solid rgb(129, 126, 126) !important ;
  width: 140px;
  height: 30px;
}
.date{
text-align: right;
margin-left: 450px;
font-size: 19px; 
color: rgb(187, 180, 180)
}
